// class지정시 날짜 배경 바뀜


.lesson_management_contents {
    h1 {
        margin-bottom: 15px;
        font-size: 20px;
    }
}

// lesson_running_contents
.lesson_running_contents {
    padding: 40px;
    border: 1px solid #eaeaea;
    border-radius: 6px;

    .lesson_running_box {
        margin-bottom: 50px;

        // lesson_runnig_top
        .lesson_running_top {

            // lesson_top_left
            .lesson_top_left {
                display: inline-block;

                p {
                    display: inline-block;
                }

                h2 {
                    display: inline-block;
                    margin-left: 10px;
                    font-size: 20px;
                }
            }

            // lesson_top_left[e]

            .lesson_top_right {
                float: right;

                h3 {
                    color: #999;
                    font-size: 14px;

                    em {
                        font-weight: 400;
                    }
                }
            }
        }

        // lesson_runnig_top[e]

        // table_1
        .table_1 {
            margin-top: 20px;
            border-top: 2px solid #1b4c88;
            width: 100%;
            border-collapse: collapse;

            thead {
                th {
                    padding: 10px 0;
                }
            }

            tbody {
                td {
                    padding: 15px 3px;
                    border: 1px solid #eaeaea;

                    &:first-child {
                        border-left: none;
                    }
                    &.background_skyblue {
                        background: #75afcd;
                    }
                }
            }

            // sub2
            &.sub2 {
                text-align: center;

                tbody {
                    tr {
                        td {
                            input {
                                padding: 10px;
                                font-size: 14px;

                                &::placeholder {
                                    color: #000;
                                    font-weight: 700;
                                }
                            }

                            label {
                                color: #000;
                                font-weight: 700;
                            }
                        }
                    }
                }
            }

            // sub2[e]

            // sub3
            &.sub3 {
                tbody {
                    tr {
                        th {
                            font-size: 15px;
                            border-top: 1px solid #eaeaea;
                            vertical-align: middle;
                        }

                        td {

                            color: #666666;
                            font-size: 14px;
                            text-align: center;

                            h4 {
                                color: #666666;
                                font-size: 14px;
                                font-weight: 400;
                                line-height: 55px;
                                text-align: left;

                                em {
                                    margin-left: 5px;
                                }
                            }
                        }

                        &:nth-child(1) {
                            td {
                                padding: 10px 20px;

                                input {
                                    display: block;
                                    padding: 10px 0;
                                    margin: 0 auto;
                                    border: 1px solid #eaeaea;
                                }

                                input:not(:nth-child(4)) {
                                    margin-bottom: 20px;
                                }
                            }
                        }

                        &:nth-child(1),
                        &:nth-child(3) {
                            td {
                                background: #fbfbfb;
                                vertical-align: middle;
                            }
                        }

                        &:nth-child(5) {
                            td {
                                color: #000;
                                font-size: 15px;
                                font-weight: 700;
                                background: #f9fafe;
                            }
                        }
                    }
                }
            }

            // sub3[e]
        }

        // table_1[e]
        textarea {
            padding: 20px;
        }
    }

    .btn_container {
        margin-top: 40px;
        text-align: center;
    }
}
// lesson_running_contents[e]

.lesson_management_contents{
    margin-bottom: 40px;
}
// mystudents_box
.mystudents_box{
    button{
        margin-bottom: 10px;
        width: 195px;
        height: 55px;
        color: #666666;
        font-size: 14px;
        background: #f8f8f8;
        border: 1px solid #eaeaea;
        border-radius: 6px;
        box-sizing: border-box;
        vertical-align: top;
        &.blue_check_line{
            color: #0d52a7;
            font-weight: 700;
            border: 1px solid #0d52a7;
        }
    }
}
// mystudents_box[e]
